.contact-container {
  max-width: 500px;
  margin: 40px auto;
  padding: 32px 20px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 16px rgba(0,0,0,0.08);
  text-align: center;
}

.contact-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
  margin-top: 32px;
  width: 100%;
}

.contact-card {
  background: #f7f7f7;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.06);
  padding: 18px 0;
  width: 100%;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
}

.contact-name {
  font-weight: 600;
  font-size: 1.1em;
  margin-bottom: 6px;
}

.contact-email {
  color: #555;
  font-size: 0.98em;
  word-break: break-all;
}

